import React, {Component} from 'react';

class MenuUtama extends Component{
	constructor (props){
		super(props)
		this.state={
			welcome:"Selamat Datang di Toko Buku ADHINDA"

		}
		this.ubahWelcome=this.ubahWelcome.bind(this)
	}
	ubahWelcome(e){
		e.preventDefault()
		this.setState({welcome:"Dapatkan Penawaran Terbaik Dari Kami"})
	}
	
	render (){
		return(
			<div>
			<h1> <center>{this.state.welcome}</center></h1>
			<center><a href="/" onClick={(e)=>this.ubahWelcome(e)}>Klik Disini</a></center>
			<br/>
			<h1>
			<center>
			<img src ='https://ecs7-p.tokopedia.net/img/cache/1190/wmEwCC/2020/8/26/e2cdf5fb-7f96-478a-8414-75be2f07ac4a.jpg' alt="banner" />
			</center>
			</h1>
			</div>
			)

		}
	}
	export default MenuUtama;